<template>
  <div class="repair-record-container">
    <!-- 顶部导航栏 -->
    <div class="app-header">
      <div class="header-content">
        <div class="return-button" @click="back">
          <i class="el-icon-arrow-left"></i>
        </div>
        <h1 class="page-title">报修记录</h1>
      </div>
    </div>

    <!-- 内容区域 -->
    <div class="content-wrapper">
      <!-- 骨架屏加载动画 -->
      <div v-if="initialLoading" class="skeleton-container">
        <div v-for="i in 3" :key="i" class="skeleton-card">
          <div class="skeleton-header">
            <div class="skeleton-icon"></div>
            <div class="skeleton-text" style="width: 60%"></div>
            <div class="skeleton-tag"></div>
          </div>
          <div class="skeleton-body">
            <div v-for="j in 3" :key="j" class="skeleton-item">
              <div class="skeleton-icon-small"></div>
              <div class="skeleton-text-line" :style="{'width': j === 3 ? '70%' : '80%'}"></div>
            </div>
          </div>
          <div class="skeleton-footer">
            <div class="skeleton-text-small"></div>
          </div>
        </div>
      </div>

      <!-- 空状态提示 -->
      <div v-else-if="data_list.length === 0 && !loading" class="empty-state">
        <i class="el-icon-document-remove empty-icon"></i>
        <p>暂无报修记录</p>
      </div>

      <!-- 报修记录列表 -->
      <transition-group v-else name="fade-list" tag="div">
        <div v-for="(item, index) in data_list" :key="item.id || index" class="record-card" @click="toDetail(item.id)">
          <div class="card-header">
            <div class="repair-info">
              <i class="el-icon-tickets info-icon"></i>
              <div class="info-text">{{ item.info }}</div>
            </div>
            <div class="status-tag" :class="getStatusClass(item.status)">
              {{ item.status }}
            </div>
          </div>
          
          <div class="card-body">
            <div class="info-item">
              <i class="el-icon-time icon"></i>
              <span class="label">报修时间：</span>
              <span class="value">{{ item.start_time }}</span>
            </div>
            
            <div class="info-item">
              <i class="el-icon-location icon"></i>
              <span class="label">报修寝室：</span>
              <span class="value">{{ item.address + " " + item.address3  || '待分配' }}</span>
            </div>
            
            <div class="info-item" v-if="item.workerName">
              <i class="el-icon-user-solid icon"></i>
              <span class="label">报修人：</span>
              <span class="value">{{ item.name + ' ' + item.phone || '无' }}</span>
            </div>
          </div>
          
          <div class="card-footer">
            <div class="footer-text">点击查看详情</div>
            <i class="el-icon-arrow-right arrow-icon"></i>
          </div>
        </div>
      </transition-group>

      <div class="bottom-tip" v-show="data_list.length > 0">
        <div v-if="loadingMore" class="loading-more">
          <i class="el-icon-loading"></i>
          <span>加载中...</span>
        </div>
        <span v-else-if="isAllData">已加载全部记录</span>
        <span v-else>上拉加载更多</span>
      </div>
    </div>
  </div>
</template>

<script>
export default {
  data() {
    return {
      data_list: [],
      initialLoading: true,
      loadingMore: false,
      workerId: localStorage.getItem('dormitory_workerId'),
      offset: 0, // 使用offset而不是page，因为后端将page作为offset使用
      scrollTimer: null,
      isLoading: false,
      isAllData: false,
      errorCount: 0,
      maxErrorCount: 3
    }
  },
  computed: {
    loading() {
      return this.initialLoading || this.loadingMore;
    }
  },
  mounted() {
    this.initData();
    window.addEventListener('scroll', this.handleScroll);
  },
  methods: {
    // 初始化数据
    async initData() {
      try {
        this.initialLoading = true;
        this.offset = 0;
        this.isAllData = false;
        this.data_list = [];
        await this.loadData();
      } catch (error) {
        console.error("初始化数据失败:", error);
        this.$message.error("数据加载失败，请稍后重试");
      } finally {
        this.initialLoading = false;
      }
    },
    
    // 加载数据
    async loadData() {
      if (this.isLoading || this.isAllData) return;
      
      if (this.errorCount >= this.maxErrorCount) {
        this.isAllData = true;
        return;
      }
      
      this.isLoading = true;
      this.loadingMore = this.offset > 0;
      
      try {
        // 注意：这里传递的offset参数，后端会将其乘以10作为实际偏移量
        const response = await this.$axios.get(
          `/record/getRecordsByWId2?workerId=${this.workerId}&page=${this.offset}`
        );
        
        if (response.data.length === 0) {
          this.isAllData = true;
          return;
        }
        
        // 处理数据
        const newData = response.data.map(item => {
          let statusText = '';
          switch(item.status) {
            case 0: statusText = '待维修'; break;
            case 1: statusText = '已维修'; break;
            case 2: statusText = '已取消'; break;
            case 3: statusText = '已转服务商'; break;
            default: statusText = '未知状态';
          }
          
          // 格式化时间
          let formattedTime = item.start_time;
          if (item.start_time && item.start_time.length >= 19) {
            formattedTime = item.start_time.substring(0, 10) + ' ' + item.start_time.substring(11, 19);
          }
          
          return {
            ...item,
            status: statusText,
            start_time: formattedTime
          };
        });
        
        // 如果是第一页，直接替换数据
        if (this.offset === 0) {
          this.data_list = newData;
        } else {
          this.data_list = [...this.data_list, ...newData];
        }
        
        // 如果返回的数据少于10条，说明已加载全部
        if (response.data.length < 10) {
          this.isAllData = true;
        }
        
        this.offset += 1; // 增加offset值，用于下一次请求
        this.errorCount = 0;
      } catch (error) {
        console.error("获取报修记录失败:", error);
        this.errorCount += 1;
        
        if (this.offset === 0) {
          this.$message.error("获取报修记录失败，请稍后重试");
        } else {
          this.$message.error("加载更多记录失败");
        }
      } finally {
        this.isLoading = false;
        this.loadingMore = false;
      }
    },
    
    // 滚动处理 - 使用防抖优化
    handleScroll() {
      if (this.scrollTimer) {
        clearTimeout(this.scrollTimer);
      }
      
      this.scrollTimer = setTimeout(() => {
        if (this.isScrollToBottom() && !this.isLoading && !this.isAllData) {
          this.loadData();
        }
      }, 150);
    },
    
    // 判断是否滚动到底部
    isScrollToBottom() {
      const scrollTop = document.documentElement.scrollTop || document.body.scrollTop;
      const clientHeight = document.documentElement.clientHeight || document.body.clientHeight;
      const scrollHeight = document.documentElement.scrollHeight || document.body.scrollHeight;
      
      return scrollTop + clientHeight >= scrollHeight - 50;
    },
    
    getStatusClass(status) {
      switch(status) {
        case '待维修': return 'status-pending';
        case '已维修': return 'status-completed';
        case '已取消': return 'status-cancelled';
        case '已转服务商': return 'status-transferred';
        default: return '';
      }
    },
    
    back() {
      this.$router.push({ path: '/worker/index' });
    },
    
    toDetail(id) {
      this.$router.push({
        name: 'workerDetail',
        params: { id: id }
      });
    }
  },
  beforeDestroy() {
    window.removeEventListener('scroll', this.handleScroll);
    if (this.scrollTimer) {
      clearTimeout(this.scrollTimer);
    }
  }
}
</script>

<style scoped>
.repair-record-container {
  min-height: 100vh;
  background: linear-gradient(135deg, #f5f7fa 0%, #e4e7ed 100%);
  font-family: 'PingFang SC', 'Helvetica Neue', Arial, sans-serif;
  padding-bottom: 20px;
}

