.app-header {
  background: linear-gradient(135deg, #409EFF 0%, #337ecc 100%);
  padding: 15px 0;
  box-shadow: 0 4px 12px rgba(64, 158, 255, 0.2);
  position: sticky;
  top: 0;
  z-index: 10;
}

.header-content {
  display: flex;
  align-items: center;
  max-width: 1200px;
  margin: 0 auto;
  padding: 0 20px;
  position: relative;
}

