  white-space: nowrap;
}

.status-pending {
  background: rgba(255, 152, 0, 0.12);
  color: #ff9800;
  border: 1px solid rgba(255, 152, 0, 0.2);
}

.status-completed {
  background: rgba(103, 199, 37, 0.12);
  color: #67c723;
  border: 1px solid rgba(103, 199, 37, 0.2);
}

.status-cancelled {
  background: rgba(158, 158, 158, 0.12);
  color: #9e9e9e;
  border: 1px solid rgba(158, 158, 158, 0.2);
}

.status-transferred {
  background: rgba(106, 17, 203, 0.1);
  color: #6a11cb;
  border: 1px solid rgba(106, 17, 203, 0.2);
}

.card-body {
  padding: 18px 20px;
}

.info-item {
  display: flex;
  align-items: center;
  margin-bottom: 14px;
}

.info-item:last-child {
  margin-bottom: 0;
}

.icon {
  font-size: 18px;
  color: #909399;
  min-width: 24px;
  margin-right: 10px;
  text-align: center;
}

.label {
  font-size: 0.95rem;
  color: #606266;
  min-width: 75px;
}

.value {
  font-size: 0.95rem;
