  font-weight: 500;
  word-break: break-all;
  flex: 1;
}

.card-footer {
  display: flex;
  justify-content: space-between;
  align-items: center;
  padding: 12px 20px;
  background: #f8fafc;
  border-top: 1px solid #f0f2f5;
}

.footer-text {
  font-size: 0.9rem;
  color: #909399;
}

.arrow-icon {
  font-size: 16px;
  color: #c0c4cc;
  transition: transform 0.3s;
}

.record-card:hover .arrow-icon {
  color: #409EFF;
  transform: translateX(3px);
}

/* 空状态样式 */
.empty-state {
  text-align: center;
  padding: 50px 20px;
  background: white;
  border-radius: 16px;
  box-shadow: 0 8px 20px rgba(0, 0, 0, 0.05);
  margin-top: 30px;
}

.empty-icon {
  font-size: 80px;
  color: #dcdfe6;
  margin-bottom: 20px;
}

.empty-state p {
  font-size: 1.1rem;
  color: #909399;
  margin-bottom: 25px;
}

/* 底部提示样式 */
.bottom-tip {
  text-align: center;
  padding: 15px 0;
  color: #909399;
  font-size: 0.9rem;
}

.loading-more {
  display: flex;
  justify-content: center;
  align-items: center;
  gap: 8px;
}

.loading-more i {
  animation: rotating 2s linear infinite;
}

@keyframes rotating {
  0% {
    transform: rotate(0deg);
  }
  100% {
    transform: rotate(360deg);
  }
}

/* 骨架屏样式 */
.skeleton-container {
  margin-top: 10px;
}

.skeleton-card {
  background: white;
  border-radius: 16px;
  box-shadow: 0 8px 20px rgba(0, 0, 0, 0.05);
  margin-bottom: 20px;
  overflow: hidden;
  padding: 20px;
}

.skeleton-header {
  display: flex;
  align-items: center;
  margin-bottom: 15px;
}

.skeleton-icon {
  width: 24px;
  height: 24px;
  background: #f0f2f5;
  border-radius: 4px;
  margin-right: 12px;
}

.skeleton-text {
  height: 20px;
  background: #f0f2f5;
  border-radius: 4px;
  flex-grow: 1;
}

.skeleton-tag {
  width: 60px;
  height: 24px;
  background: #f0f2f5;
  border-radius: 12px;
  margin-left: 10px;
}

.skeleton-body {
  margin-bottom: 15px;
}

.skeleton-item {
  display: flex;
  align-items: center;
  margin-bottom: 12px;
}

.skeleton-icon-small {
  width: 18px;
  height: 18px;
  background: #f0f2f5;
  border-radius: 4px;
  margin-right: 10px;
}

.skeleton-text-line {
  height: 16px;
  background: #f0f2f5;
  border-radius: 4px;
}

.skeleton-footer {
  display: flex;
  justify-content: space-between;
  align-items: center;
}

.skeleton-text-small {
  width: 100px;
  height: 14px;
  background: #f0f2f5;
  border-radius: 4px;
}

/* 列表动画 */
.fade-list-enter-active,
.fade-list-leave-active {
  transition: all 0.5s ease;
}
.fade-list-enter-from,
.fade-list-leave-to {
  opacity: 0;
  transform: translateY(30px);
}

/* 响应式调整 */
@media (max-width: 600px) {
  .page-title {
    font-size: 1.2rem;
  }
  
  .info-text {
    font-size: 1rem;
  }
  
  .status-tag {
    font-size: 0.8rem;
    padding: 3px 10px;
  }
  
  .label {
    min-width: 70px;
    font-size: 0.9rem;
  }
  
  .value {
    font-size: 0.9rem;
  }
}

@media (max-width: 480px) {
  .header-content {
    padding: 0 15px;
  }
  
  .return-button {
    width: 36px;
    height: 36px;
  }
  
  .page-title {
    font-size: 1.1rem;
  }
  
  .card-header {
    flex-direction: column;
    align-items: flex-start;
    gap: 10px;
  }
  
  .status-tag {
    align-self: flex-start;
  }
  
  .skeleton-card {
    padding: 15px;
  }
}
</style>